import request from '@/utils/request'


export function addCompetitionSponser(params) {
  return request({
    url: '/competition/addCompetitionSponser',
    method: 'get',
    params: params
  })
}


export function editCompetitionSponser(params) {
  return request({
    url: '/competition/editCompetitionSponser',
    method: 'get',
    params: params
  })
}


export function delCompetitionSponser(params) {
  return request({
    url: '/competition/delCompetitionSponser',
    method: 'get',
    params: params
  })
}


export function getCompetitionSponsers(params) {
  return request({
    url: '/competition/getCompetitionSponsers',
    method: 'get',
    params: params
  })
}


export function getAllCompetitions(params) {
  return request({
    url: '/competition/getAllCompetitions',
    method: 'get',
    params: params
  })
}


export function addCompetition(params) {
  return request({
    url: '/competition/addCompetition',
    method: 'get',
    params: params
  })
}


export function editCompetition(params) {
  return request({
    url: '/competition/editCompetition',
    method: 'get',
    params: params
  })
}


export function delCompetition(params) {
  return request({
    url: '/competition/delCompetition',
    method: 'get',
    params: params
  })
}






