<template>
  <div>

    <!-- 添加solution弹窗 -->
    <el-dialog :visible.sync="show.addSolution" width="90%">
      <el-form label-width="10%">
        <el-form-item label="标题">
          <el-input v-model.trim="editingItem.solution.title"></el-input>
        </el-form-item>
        <el-form-item label="来源">
          <el-input v-model.trim="editingItem.solution.link"></el-input>
        </el-form-item>

        <el-form-item label="比赛">
          <el-row>
            <el-col :span="18">
              <el-select v-model="editingItem.solution.competitionId">
                <el-option v-for="(c,ci) in competitions" :key="ci" :value="c.id" :label="c.title"></el-option>
              </el-select>
            </el-col>
            <el-col :span="6">
              <el-button type="primary" @click="addCompetition('show')">添加比赛</el-button>
            </el-col>
          </el-row>
        </el-form-item>

        <el-form-item label="Rank">
          <el-row>
            <el-col :span="2">
              <el-input v-model="editingItem.solution.rank" placeholder=""></el-input>
            </el-col>

            <el-col :offset="1" :span="1">Top</el-col>
            <el-col :span="2">
              <el-input v-model="editingItem.solution.top" placeholder=""></el-input>
            </el-col>

          </el-row>
        </el-form-item>

        <el-form-item label="Logo">
          <el-input v-model="editingItem.solution.logo" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="内容">
          <tinymce v-model="editingItem.solution.content" :width="v.tinymceWidth"></tinymce>
        </el-form-item>
      </el-form>

      <el-row>
        <el-col :span="3" :offset="10">
          <el-button :style="'width:100%'" type="primary" @click="addSolution('confirm')">写好了</el-button>
        </el-col>

      </el-row>
    </el-dialog>

    <!-- 添加比赛 -->
    <el-dialog title="添加比赛" :visible.sync="show.addCompetition">
      <el-form>

        <!-- 比赛的logo -->
        <el-form-item label="Logo">
          <span>123</span>
        </el-form-item>

        <!-- 比赛名称 -->
        <el-form-item label="名称">
          <el-input v-model="editingItem.competition.title" placeholder="比赛名称"></el-input>
        </el-form-item>

        <!-- 比赛举办方 -->
        <el-form-item label="举办方">
          <el-row>
            <el-col :span="18">
              <el-select v-model="editingItem.competition.competitionSponserId" placeholder="">
                <el-option v-for="(s,si) in competitionSponsers" :key="si" :label="s.name" :value="s.id"></el-option>
              </el-select>
            </el-col>

            <el-col :span="3">
              <el-button type="primary" @click="addCompetitionSponser('show')">添加比赛方</el-button>
            </el-col>
          </el-row>
        </el-form-item>

        <!-- 比赛链接 -->
        <el-form-item label="比赛链接">
          <el-input v-model="editingItem.competition.link" placeholder="比赛名称"></el-input>
        </el-form-item>
      </el-form>

      <el-button @click="addCompetition('confirm')" type="primary">添加比赛</el-button>

    </el-dialog>

    <!-- 添加比赛方弹窗 -->
    <el-dialog title="添加比赛方" :visible.sync="show.addCompetitionSponser">
      <el-form>
        <el-form-item label="Logo">
          <el-input v-model="editingItem.competitionSponser.logo" placeholder="比赛方名称(Kaggle)"></el-input>
        </el-form-item>
        <el-form-item label="名称">
          <el-input v-model="editingItem.competitionSponser.title" placeholder="比赛方名称(Kaggle)"></el-input>
        </el-form-item>
        <el-form-item label="网站">
          <el-input v-model="editingItem.competitionSponser.link"></el-input>
        </el-form-item>
      </el-form>

      <el-button type="primary" style="margin-top:20px;margin-left:40%" @click="addCompetitionSponser('confirm')">添加比赛方</el-button>
    </el-dialog>

    <!-- 头部 -->
    <flatGreyHead></flatGreyHead>

    <!-- 导航 -->
    <nav-bar cur="4"></nav-bar>

    <!-- 左侧 -->
    <el-button @click="addSolution('show')">添加solution</el-button>

    <!-- 中间列表 -->
    <el-table :data="list.solutions">
      <el-table-column width="100"></el-table-column>

      <!--logo -->
      <el-table-column>
        <template slot-scope="scope">
          <el-image :src="scope.row.logo">
            <!-- logo加载失败图片 -->
            <div slot="error" class="image-slot">
              <i class="el-icon-picture-outline"></i>
            </div>
          </el-image>

        </template>
      </el-table-column>

      <!-- 标题 -->
      <el-table-column>
        <template slot-scope="scope">
          <router-link :to="'/cDetail?id='+scope.row.id+'&tp=solution'">{{scope.row.solutionTitle}}</router-link>
        </template>
      </el-table-column>

      <!-- 比赛名 -->
      <el-table-column>
        <template slot-scope="scope">
          <el-tag>比赛：{{scope.row.competition.title}}</el-tag>
        </template>

      </el-table-column>

      <!--RANK 排名 -->
      <el-table-column width="100">
        <template slot-scope="scope">
          <el-tag>Rank{{scope.row.solutionRank}}</el-tag>
        </template>
      </el-table-column>

      <!--TOP 排名 -->
      <el-table-column width="100">
        <template slot-scope="scope">
          <el-tag>Top{{scope.row.solutionTop}}</el-tag>
        </template>
      </el-table-column>

      <el-table-column width="50">
        <template slot-scope="scope">
          <i class="el-icon-edit" @click="addSolution('edit',scope.row)"></i>
        </template>
      </el-table-column>

      <el-table-column width="50">
        <template slot-scope="scope">
          <i class="el-icon-delete" @click="delSolution(scope.row.id)"></i>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <el-pagination :total="page.total" :page-size="page.pageSize" :current-page="page.pageIndex" @current-change="pageChange"></el-pagination>
  </div>
</template>
<script>
import flatGreyHead from "@/components/header/flatGreyHead";
import navBar from "@/components/header/navBar";
import tinymce from "@/components/Tinymce/index";
import { getSolutions, addSolution, delSolution } from "@/api/solution.js";

import {
  addCompetitionSponser,
  editCompetitionSponser,
  delCompetitionSponser,
  getCompetitionSponsers,
  getAllCompetitions,
  addCompetition,
  editCompetition,
  delCompetition
} from "@/api/competition.js";

import qs from "qs";

export default {
  components: {
    flatGreyHead,
    tinymce,
    navBar
  },
  beforeMount() {
    //   窗口宽度
    let width = document.body.clientWidth;

    // 乘以0.9是弹窗的宽度
    width *= 0.9;

    // 再乘以0.9是label的宽度
    width *= 0.81;

    this.v.tinymceWidth = width;
  },
  created() {
    this.reloadSolutions();
  },
  data() {
    return {
      competitions: [],
      competitionSponsers: [],
      editingItem: {
        solution: {
          title: "",
          competitionId: "",
          createTime: "",
          rank: 0,
          top: 0,
          logo: "",
          content: "",
          creatorUserId: "",
          link: ""
        },
        competition: {
          logo: "",
          title: "",
          competitionSponserId: "",
          link: ""
        },
        competitionSponser: {
          logo: "",
          link: "",
          link: ""
        }
      },
      page: {
        pageSize: 10,
        pageIndex: 1
      },
      // 新建solution属性
      solution: { title: "", content: "", rank: "", year: 2019 },
      isCollapse: true,
      v: { solutionType: 9 },
      show: {
        addSolution: false,
        addCompetition: false,
        addCompetitionSponser: false
      },
      list: {
        solutions: [],
        sponsers: [],
        years: [
          2010,
          2011,
          2012,
          2013,
          2014,
          2015,
          2016,
          2017,
          2018,
          2019,
          ""
        ].reverse(),
        ranks: ["", "TOP1", "TOP2", "TOP3", "TOP5", "TOP10", "TOP30"]
      }
    };
  },
  methods: {
    // 获取所有的比赛方数据
    reloadCompetitionSponsers() {
      let that = this;
      getCompetitionSponsers().then(r => {
        if (r.code == 200) {
          that.competitionSponsers = r.data;
        } else {
          that.$message({
            message: "获取比赛方数据失败",
            type: "error"
          });
        }
      });
    },
    // 添加比赛方相关方法
    addCompetitionSponser(t) {
      let that = this;
      if (t == "show") {
        this.show.addCompetitionSponser = true;
      }

      if (t == "confirm") {
        addCompetitionSponser({
          name: that.editingItem.competitionSponser.title,
          logo: that.editingItem.competitionSponser.log,
          link: that.editingItem.competitionSponser.link
        }).then(r => {
          if (r.code == 200) {
            that.$message({
              message: "添加成功",
              type: "success"
            });
            that.reloadCompetitionSponsers();
            that.show.addCompetitionSponser = false;
          } else {
            that.$message({
              message: "添加失败",
              type: "error"
            });
          }
        });
      }
    },
    // 添加比赛相关方法
    addCompetition(t) {
      let that = this;
      if (t == "show") {
        this.show.addCompetition = true;
        this.reloadCompetitionSponsers();
      }
      if (t == "confirm") {
        addCompetition({
          title: that.editingItem.competition.title,
          logo: that.editingItem.competition.logo,
          link: that.editingItem.competition.link,
          sponserId: that.editingItem.competition.competitionSponserId
        }).then(r => {
          if (r.code == 200) {
            that.$message({
              message: "添加成功",
              type: "success"
            });
            that.show.addCompetition = false;
            that.reloadCompetitions();
          } else {
            that.$message({
              message: "添加失败",
              type: "error"
            });
          }
        });
      }
    },
    // 刷新所有比赛
    reloadCompetitions() {
      let that = this;
      getAllCompetitions().then(r => {
        if (r.code == 200) {
          that.competitions = r.data;
        } else {
          that.$message({
            message: "获取竞赛列表失败",
            type: "error"
          });
        }
      });
    },
    // 编辑
    edit(id) {
      detail({ id: id }).then(r => {
        if ((r.code = 200)) {
          (this.solution.id = id), (this.solution.title = r.data.title || "");
          this.solution.content = decodeURIComponent(r.data.content || "");
          this.v.logo = r.data.logo || "";
          this.solution.rank = r.data.rank;
          this.solution.year = r.data.year;
          this.solution.sponser = r.data.sponser;
          this.show.addSolution = true;
        }
      });
    },
    logoUploadSuccess(r, f, fs) {
      this.v.logo = r.fileCode;
    },
    // 读取比赛赞助方列表
    reload_sponsers() {
      getCompetionSponsers()
        .then(r => {
          if (r.code == 200) {
            this.list.sponsers = r.data || [];
          }
        })
        .catch();
    },
    // 删除
    delSolution(id) {
      this.$confirm("确定删除吗", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        delSolution({
          id: id
        }).then(r => {
          this.reloadSolutions();
        });
      });
    },
    // 页码修改
    pageChange(p) {
      this.page.pageIndex = p;
      this.reloadSolutions();
    },
    insertSolution() {
      // 检查参数
      if (this.solution.title == "") {
        this.$message({ message: "标题不能为空", type: "error" });
        return false;
      }
      if (this.solution.content == "") {
        this.$message({ message: "内容不能为空", type: "error" });
        return false;
      }
      if (this.solution.rank == "") {
        this.$message({ message: "排名不能为空", type: "error" });
        return false;
      }
      if (this.solution.year == "") {
        this.$message({ message: "年份不能为空", type: "error" });
        return false;
      }

      this.solution.content = encodeURIComponent(this.solution.content);

      // 如果有id，是编辑，否则是新建
      if (this.solution.id) {
        editSolution({
          id: this.solution.id,
          title: this.solution.title,
          content: this.solution.content,
          rank: this.solution.rank,
          year: this.solution.year,
          link: this.solution.link
        })
          .then(r => {
            if (r.code == 200) {
              this.solution = {};
              this.show.addSolution = false;
            }
          })
          .catch();
      } else {
        insertSolution({
          title: this.solution.title,
          content: this.solution.content,
          rank: this.solution.rank,
          year: this.solution.year,
          link: this.solution.link
        })
          .then(r => {
            if (r.code == 200) {
              this.solution = {};
              this.show.addSolution = false;
            }
          })
          .catch();
      }
    },
    addSolution(t, item) {
      let that = this;
      if (t == "show") {
        this.reloadCompetitionSponsers();
        this.solution.id = "";
        this.show.addSolution = true;
        this.reloadCompetitions();
      }

      // 如果是编辑
      if (t == "edit") {
        that.editingItem.solution.id = item.id;
        that.editingItem.solution.competitionId = item.competition.id;
        that.editingItem.solution.title = item.solutionTitle;
        that.editingItem.solution.rank = item.solutionRank;
        that.editingItem.solution.top = item.solutionTop;
        that.editingItem.solution.logo = item.logo;
        that.editingItem.solution.content = item.content;
        this.show.addSolution = true;
        this.reloadCompetitions();
      }

      if (t == "confirm") {
        let _para = {
          competitionId: that.editingItem.solution.competitionId,
          solutionTitle: that.editingItem.solution.title,
          solutionRank: that.editingItem.solution.rank,
          solutionTop: that.editingItem.solution.top,
          logo: that.editingItem.solution.logo,
          content: that.editingItem.solution.content
        };

        // 如果是编辑，将id传入
        if(that.editingItem.solution.id){
          _para.id=that.editingItem.solution.id
        }

        addSolution(_para).then(r => {
          if (r.code == 200) {
            that.$message({
              message: that.editingItem.solution.id?"编辑成功":"添加成功",
              type: "success"
            });
            that.reloadSolutions();
            that.show.addSolution = false;
          } else {
            that.$message({
              message: "添加失败",
              type: "error"
            });
          }
        });
      }
    },
    reloadSolutions() {
      getSolutions({
        pageSize: this.page.pageSize,
        pageIndex: this.page.pageIndex
      })
        .then(r => {
          if (r.code == 200) {
            this.list.solutions = r.data.content;
            this.page.total = r.data.totalElements;
          }
        })
        .catch();
    },
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  }
};
</script>
<style scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
</style>


